import React from "react";
import Header from "./components/Header";
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import APage from './components/APage';
import BPage from './components/BPage';
import CPage from './components/CPage';
import DPage from './components/DPage';
import EPage from './components/EPage';
import 'bootstrap/dist/css/bootstrap.min.css';

function App() {
  return (
    <Router>
      <div>
        <Header />
        <Switch>
          <Route path="/bio" component={APage} />
          <Route path="/APage" component={APage} />
          <Route path="/BPage" component={BPage} />
          <Route path="/CPage" component={CPage} />
          <Route path="/DPage" component={DPage} />
          <Route path="/EPage" component={EPage} />
        </Switch>
      </div>
    </Router>
  );
}

export default App;
